import { userAPI } from "../api/api";


const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE';
const SET_TOTAL_COUNT = 'SET_TOTAL_COUNT';
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';
const TOGGLE_IS_FOLLOWING_PROGRESS = 'TOGGLE_IS_FOLLOWING_PROGRESS   ';

let initialState = {
   users: [],
   pageSize: 5,
   totalUsersCount: 0,
   currentPage: 1,
   isFetching: true,
   followingInProgress: [],
}

const usersReducer = (state = initialState, action) => {
   switch (action.type) {
      case FOLLOW:
         return {
            ...state,
            users: state.users.map(u => {
               if (u.id === action.userId) {
                  return { ...u, followed: true }
               }
               return u;
            })
         };
      case UNFOLLOW:
         return {
            ...state,
            users: state.users.map(u => {
               if (u.id === action.userId) {
                  return { ...u, followed: false }
               }
               return u;
            })
         };
      case SET_USERS: {
         return { ...state, users: action.users }
      };
      case SET_CURRENT_PAGE: {
         return { ...state, currentPage: action.currentPage }
      };
      case SET_TOTAL_COUNT: {
         return { ...state, totalUsersCount: action.totalUsersCount }
      };
      case TOGGLE_IS_FETCHING: {
         return { ...state, isFetching: action.isFetching }
      };
      case TOGGLE_IS_FOLLOWING_PROGRESS: {
         return {
            ...state,
            followingInProgress: action.isFetching
               ? [...state.followingInProgress, action.userId]
               : state.followingInProgress.filter(id => id != action.userId)
         }
      };
      default:
         return state;
   }

}
export const follow = (userId) => ({ type: FOLLOW, userId });
export const unfollow = (userId) => ({ type: UNFOLLOW, userId });
export const setUsers = (users) => ({ type: SET_USERS, users });
export const setCurrentPage = (currentPage) => ({ type: SET_CURRENT_PAGE, currentPage });
export const setUsersTotalCount = (totalUsersCount) => ({ type: SET_TOTAL_COUNT, totalUsersCount });
export const toggleIsFetching = (isFetching) => ({ type: TOGGLE_IS_FETCHING, isFetching });
export const toggleFollowingProgress = (isFetching, userId) => ({ type: TOGGLE_IS_FOLLOWING_PROGRESS, isFetching, userId });

export const getUsers = (currentPage, pageSize) => async (dispatch) => {
   dispatch(toggleIsFetching(true));
   let data = await userAPI.getUsers(currentPage, pageSize)
   dispatch(toggleIsFetching(false));
   dispatch(setUsers(data.items));
   dispatch(setUsersTotalCount(data.totalCount));
}
export const pageChange = (pageNumber, pageSize) => async (dispatch) => {
   dispatch(setCurrentPage(pageNumber));
   dispatch(toggleIsFetching(true));
   let data = await userAPI.getChangePage(pageNumber, pageSize)
   dispatch(toggleIsFetching(false));
   dispatch(setUsers(data.items));
}

export const subscriptionUnfollow = (userId) => async (dispatch) => {
   dispatch(toggleFollowingProgress(true, userId));
   let data = await userAPI.deleteSubscription(userId)
   if (data.resultCode == 0) {
      dispatch(unfollow(userId))
   }
   dispatch(toggleFollowingProgress(false, userId));
}

export const subscriptionFollow = (userId) => async (dispatch) => {
   dispatch(toggleFollowingProgress(true, userId));
   let data = await userAPI.postSubscription(userId)
   if (data.resultCode == 0) {
      dispatch(follow(userId))
   }
   dispatch(toggleFollowingProgress(false, userId));
}
export default usersReducer;